.starrating {
  --ui-starrating-font-size: clamp(1.45rem, 1.45rem + 0.66vw, 2rem);
  --ui-starrating-color: #ffb100;
  --ui-starrating-focus-box-shadow: 0 0 0 var(--ui-focus-ring-width) var(--ui-focus-ring-color);
  --ui-starrating-disabled-opacity: var(--ui-disabled-opacity);
  position: relative;
  display: inline-block;
  overflow: hidden;
  font-size: var(--ui-starrating-font-size);
  line-height: 1;
  color: var(--ui-starrating-color);
  white-space: nowrap;
}
.starrating:focus-visible {
  outline: 0;
  box-shadow: var(--ui-starrating-focus-box-shadow);
}
.starrating > * > * {
  vertical-align: bottom;
}

.starrating-filled {
  position: absolute;
  top: 0;
  overflow: hidden;
}

.starrating-animate .starrating-filled {
  transition: width 0.25s ease-in-out;
}

.starrating-disabled {
  pointer-events: none;
  opacity: var(--ui-starrating-disabled-opacity);
}

.starrating-xl {
  --ui-starrating-font-size: clamp(1.65rem, 1.65rem + 1.32vw, 3rem);
}

.starrating-lg {
  --ui-starrating-font-size: clamp(1.55rem, 1.55rem + 0.99vw, 2.5rem);
}

.starrating-sm {
  --ui-starrating-font-size: clamp(1.35rem, 1.35rem + 0.33vw, 1.5rem);
}

.starrating-xs {
  --ui-starrating-font-size: 1rem;
}

